import React from 'react';

import {Header, Menu, Image, Sticky, Grid, Container, Card} from 'semantic-ui-react'
import {menuItems} from './header'
import Summary from './summary'
import Education from './education'
import Socials from './socials'
import PersonalIcon from './PersonalIcon.jpg'

export default class Main extends React.Component {
    componentWillMount() {
        this.isMobile = window.innerWidth <= 500;
    }

    isMobile = false;
    state = { activeItem: 'summary' }

    handleItemClick = (e, { name }) => 
        this.setState({ activeItem: name }
    )
    handleContextRef = contextRef => this.setState({ contextRef })

    MenuCotainer = () => {
        const {activeItem, contextRef} = this.state;
        if (!this.isMobile) {
            return (
                <Grid.Column width={3}>
                    <Sticky className="navigation_bar" context={contextRef} offset={10}>
                        <Header as='h2' icon textAlign='center'>
                            <Image src={PersonalIcon} circular />
                            <Header.Content>Chien-Ting Weng</Header.Content>
                        </Header>
                        <Menu vertical secondary>
                            {menuItems.map((info) => (
                                <Menu.Item
                                    name={info.name}
                                    onClick={this.handleItemClick}
                                    active={activeItem===info.active}
                                    key={info.name}
                                >
                                    {info.element}
                                </Menu.Item>
                            ))}
                        </Menu>
                    </Sticky>
                </Grid.Column>
            );
        } else {
            return (
                <Socials/>
            );
        }
    }

    render() {
        const menuBar = this.MenuCotainer();
        const contentWidth = this.isMobile ? 16 : 12;
        return (
            <div ref={this.handleContextRef}>
                <Grid verticalAlign='top' divided>
                        {menuBar}
                    <Grid.Column width={contentWidth}>
                        <Container className="content_container"> 
                            <Card fluid>
                                <Summary/>
                            </Card>
                            <Card fluid>
                                <Education/>
                            </Card>
                        </Container>
                    </Grid.Column>
                </Grid>
            </div>
        );
    }
}


